.header{
    display: flex;
    border-bottom: 1px solid #f2f2f2;
    position: fixed;
    z-index:999;
    top:0;
    left: 0;
    right: 0;
    background-color: #fff;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.header--logo{
    width:20%;
}

.header--logo img{
    padding-top: 10px;
    max-width:100%;
}

.header--search{
    width: 40%;
    text-align: center;
}

.header--search input[type=text]{
    display: inline-block;
    padding: 8px;
    text-align: center;
}

.header--btns{
    width: 40%;
    text-align: right;
    padding: 10px;
}

.header--btns--signIn, .header--btns--Register{
    padding: 10px;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
}

.header--btns--signIn{
    background-color: #0095f6;
    color: #fff;
    border-radius: 5px;
}

.header--btns--Register{
    color: #0095f6;
}
