.footer{
    display: flex;
    border-top: 0.1px solid #f2f2f2;
    position: fixed;
    z-index:999;
    bottom:0;
    left: 0;
    right: 0;
    background-color: #fff;
    align-items: center;
    padding: 10px;
    height: 50px;
    width: 100%;
}

.footer--bts{
    display: flex;
    padding: 20px 40px;
    width: 100%;
    justify-content: center;
    align-content: center;
}


.footer--bts--search,
.footer--bts--video,
.footer--bts--store,
.footer--bts--foto{
   margin-left: 50px;
}


.footer--bts--foto img{
    border-radius: 50%;
    width: 20px;
    height: 20px;
}