@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);

*{
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  box-sizing: border-box;
}

.header{
    display: -webkit-flex;
    display: flex;
    border-bottom: 1px solid #f2f2f2;
    position: fixed;
    z-index:999;
    top:0;
    left: 0;
    right: 0;
    background-color: #fff;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
}

.header--logo{
    width:20%;
}

.header--logo img{
    padding-top: 10px;
    max-width:100%;
}

.header--search{
    width: 40%;
    text-align: center;
}

.header--search input[type=text]{
    display: inline-block;
    padding: 8px;
    text-align: center;
}

.header--btns{
    width: 40%;
    text-align: right;
    padding: 10px;
}

.header--btns--signIn, .header--btns--Register{
    padding: 10px;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
}

.header--btns--signIn{
    background-color: #0095f6;
    color: #fff;
    border-radius: 5px;
}

.header--btns--Register{
    color: #0095f6;
}


.post{
    margin-top: 10px;
    
}
.post--img{
    width: 100%;
}

.post--img img{
    width: 100%;
}

.post--usuario{
    display: -webkit-flex;
    display: flex;
    padding: 10px;
}

.post--usuario--foto img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.post--usuario p{
    padding-left: 10px;
    padding-top: 5px;
}

.post--conteudo{
    font-size: 14px;
    padding: 10px;
}

.post--interacao--favorito,
.post--interacao--chat,
.post--interacao--compartilhar{
    margin: 10px;
    height: 30px;
    display: -webkit-flex;
    display: flex;
    cursor: pointer;
}
.footer{
    display: -webkit-flex;
    display: flex;
    border-top: 0.1px solid #f2f2f2;
    position: fixed;
    z-index:999;
    bottom:0;
    left: 0;
    right: 0;
    background-color: #fff;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
    height: 50px;
    width: 100%;
}

.footer--bts{
    display: -webkit-flex;
    display: flex;
    padding: 20px 40px;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}


.footer--bts--search,
.footer--bts--video,
.footer--bts--store,
.footer--bts--foto{
   margin-left: 50px;
}


.footer--bts--foto img{
    border-radius: 50%;
    width: 20px;
    height: 20px;
}
