
.post{
    margin-top: 10px;
    
}
.post--img{
    width: 100%;
}

.post--img img{
    width: 100%;
}

.post--usuario{
    display: flex;
    padding: 10px;
}

.post--usuario--foto img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.post--usuario p{
    padding-left: 10px;
    padding-top: 5px;
}

.post--conteudo{
    font-size: 14px;
    padding: 10px;
}

.post--interacao--favorito,
.post--interacao--chat,
.post--interacao--compartilhar{
    margin: 10px;
    height: 30px;
    display: flex;
    cursor: pointer;
}